import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, MenuController, NavParams } from 'ionic-angular';
import { Developments, Properties, Cities , Api } from '../../providers';

@IonicPage()
@Component({
  selector: 'page-prop-enlace',
  templateUrl: 'prop-enlace.html',
})
export class PropEnlacePage {
  showToolbar: boolean = false;
  cardItems: any = [];
  btnDetail: boolean = false;
  public items : any;
  public type : any;
  constructor(public api : Api, public cities: Cities, public ref: ChangeDetectorRef, public NavParams: NavParams, public developments: Developments, public properties: Properties, public menu: MenuController, public navCtrl: NavController) {
    ////console.log("prop-enlace");
    this.type = this.NavParams.data.length > 0 ? this.NavParams.data : false;
    this.loadDevelopments();
    this.initializeItems();
    ////console.log(properties);
  }

  ionViewDidEnter() {
    this.menu.enable(true);
  }

  initializeItems() {
    this.items = this.cardItems;

  }

  getItems(ev: any) {

    this.initializeItems();
    const val = ev.target.value;

    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        ////console.log(item);
        return (item.namePropertyEs.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }

  onScroll($event: any) {
    let scrollTop = $event.scrollTop;
    this.showToolbar = scrollTop >= 80;
    this.ref.detectChanges();
  }

  detail(id) {

    if (this.btnDetail)
    

    this.btnDetail = true;

    this.properties.query(id).subscribe(data => {
      this.cities.flag = 3;
      this.properties.property = data.json().data;

   
      this.navCtrl.push('PropertyDetailPage', 3);
      this.btnDetail = false;

    });


  } 

  loadDevelopments() {
    var type = this.type;
    var $this = this;
    ////console.log(this.properties.propertiesEnlaceb);

    this.properties.propertiesEnlaceb.forEach((value, key, index) => {

  
      var integer = 0;
      var integer2 = 0;

      if (value.descriptionEs.length > 130) {          
        value.descriptionEs = value.descriptionEs.substring(0, 145);
        value.descriptionEs = value.descriptionEs + '...';   
      }

      //Tipo de moneda
      integer   = parseInt(this.api.filtre.moneda, 10);
      integer2  = parseInt(value.currencyid , 10);

      if(this.api.filtre.moneda){
        if( !( integer2 == integer) ){
          return;
        }
      }
      //-

      //Precio minimo
      integer   = parseInt(this.api.filtre.priceMin, 10);
      integer2  = parseInt(value.price , 10);

      if(this.api.filtre.priceMin  && value.price ){
        if(!( integer <= integer2 )){
          return;
        }
      }
      //-

      //Precio maximo
      integer   = parseInt(this.api.filtre.priceMax, 10);
      integer2  = parseInt(value.price  , 10);

      if(this.api.filtre.priceMax && value.price){
        if(!( integer >= integer2 )){
          return;
        }
      }
      //-

      //Recamaras minimas
      integer   = parseInt(this.api.filtre.bedMin, 10);
      integer2  = parseInt(value.bedrooms , 10);
      if(this.api.filtre.bedMin && value.bedrooms){
        if(!(integer <= integer2 )){
          return;
        }
      }
      //-

      //Recamaras maximas
      integer   = parseInt(this.api.filtre.bedMax, 10);
      integer2  = parseInt(value.bedrooms  , 10);

      if(this.api.filtre.bedMax && value.bedrooms){
        if(!(integer >= integer2 )){
          return;
        }
      }
      //-

      //Metros 2 minimos
      integer   = parseInt(this.api.filtre.m2Min, 10);
      integer2  = parseInt(value.m2c, 10);

      if(this.api.filtre.m2Min && value.m2c){

        if(!(integer <= integer2)){
          return;
        }
      }
      //-

      //Metros 2 Maximos
      integer   = parseInt(this.api.filtre.m2Max, 10);
      integer2  = parseInt(value.m2c, 10);

      if(this.api.filtre.m2Max && value.m2c){
     // ////console.log(integer2 ,'--', integer, value.developmentNameEs);

        if(!(integer >= integer2)){
          return;
        }
      }
      //-

      //Cajones estacionamientos maximas
      // integer   = parseInt(this.api.filtre.parkMax, 10);
      // integer2  = parseInt(value.m2c  , 10);  
      
      // if(this.api.filtre.parkMax && value.m2c){  
      //   if(!(integer >= integer2)){
      //     return;
      //   }
      // }
      //-

      //Cajones estacionamientos minimos
      // integer   = parseInt(this.api.filtre.parkMin, 10);
      // integer2  = parseInt(value.parkingT, 10);

      // if(this.api.filtre.parkMin && value.parkingT){
      //   if(!(integer <= integer2)){
      //     return;
      //   }
      // }
      //-





      if(type){
        if (value.stateid == type) {
          $this.cardItems.push(value);
        }else{
          return;
        }
      } else {
        $this.cardItems.push(value);

      }




    });
  }

  filtre(){
    this.navCtrl.push('FiltrePage', 3);
  }
}











